import {request} from "../axios/request";

const baseUrl = '/api/admin'

const TagsApi = {}

TagsApi.All = () => {
  return request({
    url: `${baseUrl}/allTags`,
    method: 'get'
  })
}

TagsApi.loadTags = () => {
  return request({
    url: `${baseUrl}/loadTags`,
    method: 'get'
  })
}

TagsApi.addTag = tag => {
  return request({
    url: `${baseUrl}/addTag`,
    method: 'post',
    data: tag
  })
}

TagsApi.update = tag => {
  return request({
    url: `${baseUrl}/updateTag`,
    method: 'put',
    data: tag
  })
}

TagsApi.delete = id => {
  return request({
    url: `${baseUrl}/deleteTag`,
    method: 'delete',
    params: {
      id
    }
  })
}

export default TagsApi